* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  opacity: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.calendly-badge-content:hover {
  background-color: rgb(0, 89, 255) !important;
}

#navbar {
  background-color: black;
}
.nav-link {
  color: #28a745;
}
.nav-link:hover {
  color: #22ff33;
}
#btn-pro {
  background-color: #222;
}
#btn-retro {
  background-image: linear-gradient(
    #fc600a,
    #fccc1a,
    #b2d732,
    #347c98,
    #4424d6,
    #c21460
  );
  font-weight: 500;
  font-size: 1.1em;
}

/***** ABOUT ******/
.canvas {
  top: 0;
  left: 0;
  max-width: 100%;
  height: 100vh;
  z-index: -10;
  position: fixed;
}
.background {
  top: 0;
  left: 0;
  background: linear-gradient(35deg, rgb(17, 42, 97), rgb(60, 9, 73));
  width: 100%;
  height: 100vh;
  z-index: -10;
}
#easle {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: -10;
}

#name {
  font-family: "Courier New";
  font-size: 3rem;
  font-weight: 600;
  margin-top: 1.8em;
}

/* PROGRESS BAR */

:root {
  --disp: none;
}

#open-source {
  font-family: sans-serif;
  text-align: center;
  width: 100%;
}

#line-contributions {
  display: flex;
  justify-content: space-around;
}

.progbar {
  width: 100%;
  display: flex;
  height: 5%;
  background-color: transparent;
  border: solid rgb(61, 216, 255) 2px;
  border-radius: 20px;
  align-items: center;
  position: relative;
}

#progress-bar {
  background-color: rgb(61, 216, 255);
  height: 90%;
  border: solid rgb(61, 216, 255) 2px;
  border-radius: 15px;
  text-align: end;
}

.progbar > span {
  width: 5vh;
  height: 100%;
  border: solid green 2px;
  border-radius: 50%;
  position: absolute;
  z-index: 20;
}

#first-span {
  left: 0%;
}
#second-span {
  left: 48%;
}
#third-span {
  right: 0px;
}

.checkmark {
  background-color: transparent;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: var(--disp);
}
.checkmark:after {
  left: 20%;
  top: 3%;
  width: 40%;
  height: 70%;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.tool {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}

.tool .tooltiptext {
  visibility: hidden;
  width: fit-content;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;

  /* Position the tooltip */
  position: relative;
  top: -5%;
  z-index: 1;
}

.tool:hover .tooltiptext {
  visibility: visible;
}

.svg {
  font-size: 2em;
}
.anim {
  animation: wave 1.5s alternate infinite ease-in-out;
}
@keyframes wave {
  to {
    transform: translatey(10px);
  }
}

.Boot {
  color: purple;
}
.Js {
  color: orange;
}
.Css {
  color: #3c9cd7;
}
.Codepen {
  color: white;
}
.Git {
  color: #f05033;
}
.Trello {
  color: blue;
}
.Py {
  color: green;
}
.React {
  color: #61dafb;
}
@media (prefers-reduced-motion: no-preference) {
  .React,
  .Py {
    animation: React-spin infinite 20s linear;
