}

.svg-holder {
  margin: auto;
}

#para {
  margin-top: 1.5em;
}
/****** PROJECTS ******/
.card-deck {
  /* margin: 0; */
  /* padding: 2em 0; */
  overflow: hidden;
}
/* .card {
  flex-basis: 30% !important;
} */
.card-title {
  font-size: 1.8rem;
}
.btn-purple {
  background-color: #28187b;
  color: white;
  font-weight: 600;
  font-size: 1.2em;
}
.btn-purple:hover {
  color: white;
  background-color: #347c98;
}

.card-holder {
  width: 100%;
  height: 50%;
}
.card-img-top {
  object-fit: cover;
  object-position: 0;
  height: 100%;
}

.hovered-img {
  transition: transform 0.5s ease;
  transform: scale(1.1);
}

/****** CONTACT ******/
.icon-btn {
  width: 100%;
  display: flex;
  justify-content: center;
}
.holder {
  color: white;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 2em;
  max-width: 5em;
  height: 5em;
  overflow: hidden;
  border-radius: 50%;
  margin-top: 0.1rem;
  -webkit-transition: max-width 1s ease;
  transition: max-width 1s ease;
}

.icon {
  font-size: 4em;
  margin: 3rem 2rem 3rem 1rem;
  border-radius: 50%;
}

/* Certifications */

.title {
  font-size: 1rem;
}

@media only screen and (min-width: 400px) {
  .title {
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 550px) {
  .title {
    font-size: 1.1rem;
  }
}

@media only screen and (min-width: 700px) {
  .title {
    font-size: 1.2rem;
  }
}

/* MODULES */

#modules {
  width: 100%;
  min-height: 100vh;
  color: black;
  font-weight: bold;
}

.dropdown-menu {
  min-width: 1em;
}

/* .mod-description {
  max-height: 100px;
} */

#btn-filter {
  display: unset;
}

.arrow {
  border: solid black;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 5px;
}
.arrow:focus {
  outline: none;
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.heading-2 {
  color: #22ff33;
  margin-top: 2em;
}

/* --------------------------------------- */

@media only screen and (max-width: 600px) {
  .icon {
    font-size: 3em;
    margin: 3rem 1rem 0rem 1rem;
  }
  .holder {
    overflow: visible;
    width: fit-content;
    flex-direction: column;
  }
}
.GH {
  background-color: black;
}
.Li {
  background-color: #0062f5;
}
.FB {
  background-color: rgb(0, 89, 255);
}
.fCC {
  background-color: green;
}

.holder:hover,
.icon-btn:focus > .holder {
  transition: all 1s ease;
  max-width: 700px;
  border-radius: 0;
  padding-right: 1rem;
}

.row {
  --bs-gutter-x: 0;
}
